@import "./common";

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.window {
  position: relative;
  background: rgb(21, 43, 67);
  border-radius: 5px;
  // border: rgb(39, 42, 46) solid 2px;
  padding: 15px;
  box-shadow: rgb(0, 0, 0) 2px 2px 10px;
  z-index: 10;
  min-width: 320px;
  // display: flex;
  // justify-content: space-between;
  // align-items: center;
  // flex-flow: wrap;
  //flex-direction: column;
  overflow: hidden;
  width: 1170px;
  margin: auto;
}
.container-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: wrap;
}
.background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(20, 20, 20, 0.5);
  // opacity: 0.4;
  backdrop-filter: blur(2px);
}

.title {
  color: #fff;
  font-size: 30px;
  margin-bottom: 40px;
  text-align: center;
}
.modal-input {
  float: left;
  margin-bottom: 15px;
  //display: flex;
  //justify-content: space-around;
  //align-items: center;
  //flex-flow: wrap;
  // flex-direction: column;
}
